import { IBasicDataFeed, ResolutionString } from '@charting-library'
import moment from "moment-timezone";
import {subscribe, unsubscribe} from './stream';

export function randomInteger(min: number, max: number) {
    let rand = min + Math.random() * (max + 1 - min);
    return Math.floor(rand);
}

export const datafeed: IBasicDataFeed = {
    onReady: callback => {
        setTimeout(() => callback({
            supported_resolutions: ['1' as ResolutionString]
        }))
    },

    searchSymbols: () => {},
    resolveSymbol: (symbolName, onSymbolResolveCallback) => {
        setTimeout(() => {
            onSymbolResolveCallback({
                name: symbolName,
                description: symbolName,
                ticker: symbolName,
                full_name: '',
                exchange: '',
                listed_exchange: '',
                format: 'price',
                supported_resolutions: ['1' as ResolutionString],
                type: 'crypto',
                session: '24x7',
                minmov: 1,
                pricescale: 100, // 	or 100
                timezone: 'Etc/UTC',
                has_intraday: true,
                has_daily: true,
                has_weekly_and_monthly: true,
                currency_code: symbolName,

                has_no_volume: true,
            })
        })
    },

    getBars: (symbolInfo, interval, periodParams, onHistoryCallback, onErrorCallback) => {
        if (!periodParams.firstDataRequest) {
            return onHistoryCallback([])
        }

        const klines = [
            {
                time: moment().unix() * 1000,
                open: randomInteger(50, 180.34),
                high: randomInteger(100, 180.99),
                low: randomInteger(0, 178.57),
                close: randomInteger(0, 179.85)
            }
        ]

        onHistoryCallback(klines)
    },

    subscribeBars: (symbolInfo, interval, onRealtimeCallback, subscribeUID, onResetCacheNeededCallback) => {
        subscribe(cb => onRealtimeCallback(cb))
    },

    unsubscribeBars: (subscriberUID) => {
        unsubscribe()
    },
}
