import {backendWSAddress} from "../../../api/constants";
import { SubscribeBarsCallback } from "@charting-library";
import {WebSocketEvent} from "../../../api/methods/fetchCurrency";
import moment from "moment-timezone";

export function openWebsocket(currency: string): WebSocket {
    const connection = new WebSocket(`${backendWSAddress}/${currency}`)

    window.websocket = connection
    return connection
}

export function closeWebsocket() {
    if (window.websocket) {
        window.websocket.close()
        window.websocket = void 0
    }
}

export function subscribe(callback: SubscribeBarsCallback) {
    if (window.websocket) {
        window.websocket.addEventListener('message', (event) => {
            const data = JSON.parse(event.data) as WebSocketEvent

            if (data.type === 'CHARTS' && data.data[0]) {
                callback(data.data[0])
            }
        })
    }
}

export function unsubscribe() {
    closeWebsocket()
}
